/* src/App.css */
body {
  background-color: #1e1e1e;
  color: #fff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.App {
  text-align: center;
  width: 80%;
  max-width: 600px;
}

.character-creation,
.boss-fight {
  background: #333;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  margin: 10px 0;
}

input, select, button {
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
  border: none;
  font-size: 1em;
}

button {
  background-color: #ffcc00;
  color: #000;
  cursor: pointer;
}

button:hover {
  background-color: #ffaa00;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  margin: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

img {
  max-width: 100%;
  height: auto;
  border: 2px solid #ffcc00;
  border-radius: 10px;
  margin: 20px 0;
}

.highlight {
  background-color: #ffcc00;
  color: #000;
}

.add-chore {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.add-chore input {
  margin: 5px 0;
}

.add-chore label {
  display: flex;
  align-items: center;
  margin: 5px 0;
}

.health-bar {
  background: #ff6666;
  border-radius: 10px;
  overflow: hidden;
  height: 20px;
  margin: 20px 0;
}

.health {
  background: #66ff66;
  height: 100%;
}

.dice-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
}

.die {
  position: relative;
  width: 100px;
  height: 100px;
  transform-style: preserve-3d;
  transform: rotateX(-30deg) rotateY(-45deg);
  transition: transform 3s;
}

.die.rolling {
  animation: roll 3s infinite;
}

@keyframes roll {
  0% { transform: rotateX(0deg) rotateY(0deg); }
  100% { transform: rotateX(360deg) rotateY(360deg); }
}

.die .face {
  position: absolute;
  width: 100px;
  height: 100px;
  background: #ffcc00;
  border: 1px solid #000;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
}

.die .face-1 { transform: rotateY(0deg) translateZ(50px); }
.die .face-2 { transform: rotateY(90deg) translateZ(50px); }
.die .face-3 { transform: rotateY(180deg) translateZ(50px); }
.die .face-4 { transform: rotateY(-90deg) translateZ(50px); }
.die .face-5 { transform: rotateX(90deg) translateZ(50px); }
.die .face-6 { transform: rotateX(-90deg) translateZ(50px); }

.randomize {
  background-color: #ffcc00;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  margin: 20px 0;
}

.randomize:hover {
  background-color: #ffaa00;
}
